// блок комментария панорамы
.comment-wrapper__panorama {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding-top: 18px;
  padding-right: 12px;
  padding-bottom: 20px;
  padding-left: 17px;

  background-color: #f2f2f2;
}

// стили комментариев
.comment-wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  width: 280px;

  margin: 0 auto;
  margin-bottom: 20px;
  padding-top: 18px;
  padding-left: 18px;
  padding-right: 11px;

  background-color: #f2f2f2;
  color: $darkblue-color;
}

// заголовок
.comment-wrapper__header {
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  text-align: left;
}

// время комментария
.comment-wrapper__comment-time {
  font-size: 12px;
  font-weight: 300;
  line-height: 24px;
  text-align: left;
  text-transform: uppercase;
}

// текст комментария
.comment-wrapper__comment-text {
  margin: 0;
  margin-bottom: 9px;

  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
}

// текст лайка
.comment-wrapper__like {
  margin-bottom: 20px;

  font-size: 12px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
  color: $pink-color;
  text-transform: uppercase;
  margin-left: 32px;

  &::before {
    position: absolute;
    width: 13px;
    height: 12px;
    margin-left: -32px;
    margin-top: 6px;

    content: "";

    background-image: url('data:image/svg+xml,%3Csvg version="1.0" xmlns="http://www.w3.org/2000/svg" width="12.545" height="11.99" viewBox="521.939 473.064 12.545 11.99"%3E%3Cpath fill="%23d22856" d="M528.174 485.055a.737.737 0 01-.552-.249c-3.798-4.324-4.644-5.298-4.858-5.572h-.004a3.866 3.866 0 01-.821-2.373 3.803 3.803 0 013.799-3.799c.934 0 1.804.32 2.484.898a3.847 3.847 0 012.463-.898 3.803 3.803 0 013.799 3.799c0 .909-.318 1.754-.896 2.381l-4.86 5.561a.735.735 0 01-.554.252zm-4.245-6.706c.208.255 2.471 2.839 4.243 4.858l4.323-4.944c.34-.369.52-.86.52-1.399a2.333 2.333 0 00-2.33-2.331c-.729 0-1.421.344-1.9.943a.76.76 0 01-.585.275.727.727 0 01-.575-.294 2.333 2.333 0 00-1.887-.925 2.333 2.333 0 00-2.33 2.331c0 .678.275 1.171.507 1.467l.014.019z"/%3E%3C/svg%3E');
    background-repeat: no-repeat;
  }
}

// СТИЛИ ДЛЯ ПЛАНШЕТА

@media (min-width: $tablet-width) {
  // обертка комментария
  .comment-wrapper {
    display: flex;
    flex-direction: column;
    width: 300px;
    min-height: 155px;

    padding-top: 24px;
    padding-left: 27px;
    padding-bottom: 22px;
    margin: 0;
    margin-bottom: 20px;
  }

  // отступ для заголовка комментария
  .comment-wrapper__header {
    margin-bottom: 0;
  }

  // счетчик лайков
  .comment-wrapper__like {
    margin-bottom: 0;
    margin-left: 0;
    margin-top: auto;

    &::before {
      margin-left: 228px;
      margin-top: 5px;
    }
  }

  // обертка комментария панорамы
  .comment-wrapper__panorama {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;

    width: 620px;
    margin-bottom: 50px;

    padding-top: 24px;
    padding-left: 27px;
    padding-bottom: 0;
    padding-right: 30px;

    background-color: #f2f2f2;
  }

  .comment-wrapper__comment-text {
    margin-bottom: 0;
  }

  // положения блоков для комментария в панораме
  .comment-wrapper__panorama .comment-wrapper__header {
    order: -3;
  }

  .comment-wrapper__panorama .comment-wrapper__like {
    order: -2;
    margin-right: 29px;

    &::before {
      margin-left: 106px;
      margin-top: 5px;
    }
  }

  .comment-wrapper__panorama .comment-wrapper__comment-text {
    order: -1;
    margin-bottom: 24px;
  }
}

// СТИЛИ ДЛЯ ДЕСКТОПА

@media (min-width: $desktop-width) {
  // комментарий
  .comment-wrapper__panorama {
    width: 940px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 49px;
  }
}
